 .i{
    display: flex;
    height: 100vh;
    
 }
 .i-left{
     flex: 1;
     display: flex;
     align-items: center;
     justify-content: center;
 }
 .i-right{
     flex: 1;
     position: relative;
 }
 .i-left-wrapper{
     padding: 50px;
     height: 50%;
     display: flex;
     flex-direction: column;
     justify-content: space-between;
 }
 .i-intro{
     font-size: 30px;
     font-weight: 300;
 }
 .i-name{
     font-size: 50px;
 }
 .i-tittle{
     height: 35px;
     overflow: hidden;
 }
 .i-tittle-wrapper{ height: 50px;
     animation: move 10s ease-in-out infinite alternate;
 }
 @keyframes move{
     25%{ transform: translateY(-50px);}
     50%{transform: translateY(-100px);}
     75%{transform: translateY(-150px);}
     100%{transform: translateY(-200px);}
 }
 .i-tittle-item{
     height: 50px;
     font-size: 30px;
     font-weight: bold;
     color: blueviolet;
     display: flex;
     align-items: center;
 }
 .idesc{
     padding: 10px;
 }
 .i-scroll{
     height: 50px;
     width: 30Spx;
     position: absolute;
     bottom: 20px;
 }
 .i-img{
     height: 100%;
     width: 100%;
     object-fit: cover;
     position: absolute;
 }
 .i-bg{
    clip-path: polygon(25% 0%, 100% 0, 100% 100%, 25% 100%, 0% 50%);
    background-color: blueviolet;
    height: 100%;
    width: 90%;
    position: absolute;
    top: 0;
    right: 0;
 }
 @media screen and (max-width: 480px) {
    .i {
      flex-direction: column;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }
  
    .i-desc{
      display: none;
    }
    
  }